.recipe {
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(71, 71, 71);
  margin: 20px;
  /* display: flex; */
  /* box-sizing: border-box; */
  /* flex-direction: column; */
  /* justify-content: space-around; */
  background: white;
  max-width: 400px;
  min-width: 400px;
  /* max-height: 10%; */
  /* min-height: 600px; */
}

.image {
  border-radius: 50%;
  width: 80px;
  height: 80px;
  margin: 20px;
}

.header {
  background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  font-size: 20px;
  cursor: pointer;
}

.content {
  align-items: center;
  margin: 20px;
}

.content h1 {
  color: white;
}

.title {
  color: black;
  font-size: 100%;
  margin: 10px 10px;
}
